import { Wheel } from 'react-custom-roulette'
import { useState } from 'react';

function PointRoulette() {
  //데이터
  const data = [
    { option: '꽝', style: { backgroundColor: 'gray', textColor: 'white' }, percentage : 50 },
    { option: 'x2배', style: { backgroundColor: 'red', textColor: 'white'  }, percentage : 20 },
    { option: 'x5배', style: { backgroundColor: 'blue', textColor: 'white'  }, percentage : 10 },
    { option: 'x20배' , style: { backgroundColor: 'white', textColor: 'blue'  }, percentage : 1 },
    { option: '-x2배' , style: { backgroundColor: 'black', textColor: 'yellow'  }, percentage : 10 },
    { option: '-x5배' , style: { backgroundColor: 'black', textColor: 'pink'  }, percentage : 8 },
    { option: '-x100배' , style: { backgroundColor: 'black', textColor: 'red'  }, percentage : 1 },
    ]

  
  const [mustSpin, setMustSpin] = useState(false);  //룰렛이 회전 애니메이션을 시작
  const [prizeNumber, setPrizeNumber] = useState(0);//당첨 인덱스

  const handleSpinClick = () => {
    if (!mustSpin) {
      // 1. 랜덤 기준점 설정
      const pivot   = Math.floor((Math.random() * 99) +1);
      let   stack   = 0; // 가중치

      let percentage = data.map((row,idx)=>{  { return row.percentage } });

      let newPrizeNumber = null; //당첨 인덱스

      percentage.some((row,idx)=>{
        //2. 가중치 누적
        stack += row;

        // 3. 누적 가중치 값이 기준점 이상이면 종료
        if(pivot <= stack){
          newPrizeNumber  = idx;
          return true;
        }

      }) 
      setPrizeNumber(newPrizeNumber);
      setMustSpin(true);
    }
  }

  const StopSpinning = () => {
    setMustSpin(false);
   // alert(data[prizeNumber].option+'에 당첨되셨습니다');
  }

  return (
    <div className="Roulette">
     <Wheel
        spinDuration={0.2} // spin속도
        startingOptionIndex={Math.floor(Math.random() * data.length)} 
        mustStartSpinning={mustSpin}
        prizeNumber={prizeNumber}
        data={data}
        onStopSpinning={StopSpinning}
      />
      <button onClick={handleSpinClick}>SPIN</button>
      <div>{prizeNumber}번 선물에 당첨됐음</div>
    </div>
  );
}

export default PointRoulette;