import 'bootstrap/dist/css/bootstrap.min.css';

import {Container,Nav, Navbar,NavDropdown} from 'react-bootstrap'
import {Route, Routes,useNavigate } from 'react-router-dom';
import React, { Component } from 'react';

import SAGONGSAPage from './404Page';
import Dialog from './WeeklyMission';
import Map from './Map';
import Shop from './ShopInside';
import Timeline from './Timeline';
import NoticeCarousel from './NoticeCarousel';
import Login from './Login';
import PointRoulette from './Roulette';
import DemoBook from './StoryBook';
import Academy from './Academy';



function clickEffect(e){
  var d=document.createElement("div");
  d.className="clickEffect";
  d.style.top=e.clientY+"px";d.style.left=e.clientX+"px";
  document.body.appendChild(d);
  d.addEventListener('animationend',function(){d.parentElement.removeChild(d);}.bind(this));
  }
  document.addEventListener('click',clickEffect);


function Header() {

  let navigate = useNavigate()
  
  return (
    



    <div className="Header">




    <Navbar expand="lg"  bg="dark" className="bg-body-tertiary">
      <Container>
        <Navbar.Brand onClick={()=>{navigate('/')}}>
          <img src={'https://i.ibb.co/9GHzc4Z/logo-mini.png'} width="100vw" />
          </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link onClick={()=>{navigate('/')}}>메인</Nav.Link>

            <NavDropdown title="세계관" id="basic-nav-dropdown">
              <NavDropdown.Item onClick={()=>{navigate('/story')}}>세계관 인트로</NavDropdown.Item>
              <NavDropdown.Item onClick={()=>{navigate('/timeLine')}}>연표</NavDropdown.Item>
              <NavDropdown.Item onClick={()=>{navigate('/map')}}>
                프로테스 지도
              </NavDropdown.Item>
              </NavDropdown>


              <NavDropdown title="라딕스 아카데미" id="basic-nav-dropdown">
              <NavDropdown.Item onClick={()=>{navigate('/academy')}} >라딕스 아카데미</NavDropdown.Item>
              <NavDropdown.Item onClick={()=>{navigate('/square')}} disabled="true" >중앙 광장</NavDropdown.Item>
              <NavDropdown.Item onClick={()=>{navigate('/garden')}} disabled="true" >라인티스크 정원</NavDropdown.Item>
              <NavDropdown.Item onClick={()=>{navigate('/dormitory')}} disabled="true" >크로커스 관</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item onClick={()=>{navigate('/basement')}} disabled="true" > 
                아카데미 지하
              </NavDropdown.Item>
              </NavDropdown>


            <NavDropdown title="폭포 아래" id="basic-nav-dropdown">
              
              <NavDropdown.Item onClick={()=>{navigate('/weeklyMission')}} disabled="true" >주간 미션</NavDropdown.Item>
              <NavDropdown.Item onClick={()=>{navigate('/store')}} disabled="true" >샤샤 상점</NavDropdown.Item>
              <NavDropdown.Item onClick={()=>{navigate('/roulette')}} disabled="true" >샤샤 룰렛</NavDropdown.Item>
          
              
             
              <NavDropdown.Divider />
              <NavDropdown.Item onClick={()=>{navigate('/myAccount')}} disabled="true" >
                마이 페이지
              </NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>


<Routes>
<Route path="/" element={<div><NoticeCarousel/></div>}/>
<Route path="/story" element={<div><DemoBook/></div>}/>
<Route path="/timeLine" element={<div><Timeline/></div>}/>
<Route path="/map" element={<Map></Map>}/>
<Route path="/academy" element={<div><Academy/></div>}/>
<Route path="/square" element={<div>--개발중--</div>}/>
<Route path="/store" element={<div><Shop /></div>}/>
<Route path="/weeklyMission" element={<div><Dialog/></div>}/>
<Route path="/roulette" element={<div><PointRoulette/></div>}/>
<Route path="/lots" element={<div>--개발중--</div>}/>
<Route path="/myAccount" element={<div>마이페이지 개발중</div>}/>
<Route path="/Join" element={<div>회원가입 개발중</div>}/>

<Route path="/login" element={<div><Login/></div>}/>



<Route path="/데니블용(기본게시판)" element={<div>나중에 캐릭터들 컨택 받을 때 마다 페이지가 새로생기는 매직을 보여주겠다</div>}/>
<Route path="*" element={<div><SAGONGSAPage/></div>}/>

</Routes>

</div>
  );
}


export default Header;
