import Form from 'react-bootstrap/Form';
import { Button } from 'react-bootstrap';

function Login(){


return(

<div className="LogIncontainer" width="100%" hight="100%">

 <img className="logo" src={'https://i.ibb.co/kBym91p/logo.png'} width="30%" />


<div className="loginForm">

<div>
<Form.Label htmlFor="inputPassword5">아이디</Form.Label>
 <Form.Control
        type="id"
        id="inputId"
        aria-describedby="idHelpBlock" />
     </div>

    <div>
      <Form.Label htmlFor="inputPassword5">비번</Form.Label>
      <Form.Control
        type="password"
        id="inputPassword"
        aria-describedby="passwordHelpBlock"
      />
       </div>

       <Button variant="outline-info" className="submit">
        로그인
      </Button>

</div>
</div>


);


}


export default Login;