import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Tooltip from 'react-bootstrap/Tooltip';
import data from './ItemData.js';

function Shop() {

    let [itemData] = useState(data);

    return (

        <div className="container">
         <div className="row">   
        <div className="col-md-8">  
        <div className="row">   
       
         {
        
          itemData.map((a,i)=>{
            return(
              <Card itemData={itemData[i]} i={i}></Card>
            )
          })
         }
         </div>   
        </div>
        <div className="col-md-4"> 장바구니 영역 ♥ </div>
        </div>
        </div>


    );
    }

function Card(props){
    return(
      <div className="col-md-3" onClick={()=>{console.log(props.itemData.itemName)}}>
      <img src={'https://i.ibb.co/CMH6njH/shop-Item' + (props.i+1) + '.gif'} width="60%" />
        <>
      {['bottom'].map((placement) => (
        <OverlayTrigger
          key={placement}
          placement={placement}
          overlay={
            <Tooltip id={`tooltip-${placement}`}>
             <strong>{props.itemData.text}</strong>.
            </Tooltip>
          }
        >
          <Button variant="secondary">{props.itemData.itemName}</Button>
        </OverlayTrigger>
      ))}
    </>
    </div>
    )
  }

 
  export default Shop;