import { useState } from 'react';
import Modal from 'react-bootstrap/Modal';
import { Button } from 'react-bootstrap';
import MapData from './MapData';
import { PlayerState, useYoutube } from "react-youtube-music-player";
import {
  IoPause,
  IoPlay,
  IoPlaySkipBack,
  IoPlaySkipForward,
  IoStop,
  IoVolumeHigh,
  IoVolumeMedium,
  IoVolumeLow,
  IoVolumeMute
} from "react-icons/io5";



function Map() {

let [Area,AreaChange] = useState(0);
const [show, setShow] = useState(false);


  return (
    <div className='MapContainer'>
      <div className='AllMap'>
      <img src={'https://i.ibb.co/kM0jsPP/AllMap.png'} width="100%" />


<div className='MapButton'>

  <ModalForMap/>

      </div>


            </div>
     
      
      </div>

  
  );

  function ModalForMap(){

    function BGM(){
      const { playerDetails, actions } = useYoutube({
        id: MapData[Area].BGMURL,
        type: "playlist"
      });
    
      const renderVolumeIcon = () => {
        if (playerDetails.volume === 0) {
          return <IoVolumeMute />;
        }
        if (playerDetails.volume <= 30) {
          return <IoVolumeLow />;
        }
        if (playerDetails.volume <= 60) {
          return <IoVolumeMedium />;
        }
        return <IoVolumeHigh />;
      };
    
      return (
        <div className="AppBGM">
          <div className="video-title">{playerDetails.title}</div>
          <div className="player-controls">
            <button onClick={actions.previousVideo} style={{backgroundColor: "transparent", border:"none"}}>
              <IoPlaySkipBack />
            </button>
            {playerDetails.state === PlayerState.PLAYING ? (
              <button className="emphasised" onClick={actions.pauseVideo} style={{backgroundColor: "transparent", border:"none"}}>
                <IoPause />
              </button>
            ) : (
              <button className="emphasised" onClick={actions.playVideo} style={{backgroundColor: "transparent", border:"none"}}>
                <IoPlay />
              </button>
            )}
            <button onClick={actions.stopVideo} style={{backgroundColor: "transparent", border:"none"}}>
              <IoStop />
            </button>
            <button onClick={actions.nextVideo} style={{backgroundColor: "transparent", border:"none"}}>
              <IoPlaySkipForward />
            </button>

          </div>
          <div className='volume-control'>
              {renderVolumeIcon()}
              <input className='volume'
                type="range"
                value={playerDetails.volume ?? 0}
                min={0}
                max={100}
                onChange={(event) => actions.setVolume(event.target.valueAsNumber)}
              />
           </div>
        </div>
      );
      
    }

    
    return (
      <>
<Button variant="link" className='MapArea0' onClick={() => (AreaChange(0),setShow(true))}></Button>
<Button variant="link" className='MapArea1' onClick={() => (AreaChange(1),setShow(true))}></Button>
<Button variant="link" className='MapArea2' onClick={() => (AreaChange(2),setShow(true))}></Button>
<Button variant="link" className='MapArea3' onClick={() => (AreaChange(3),setShow(true))}></Button>
<Button variant="link" className='MapArea4' onClick={() => (AreaChange(4),setShow(true))}></Button>
<Button variant="link" className='MapArea5' onClick={() => (AreaChange(5),setShow(true))}></Button>
<Button variant="link" className='MapArea6' onClick={() => (AreaChange(6),setShow(true))}></Button>


     <Modal 
        size="xl"
        show={show}
        onHide={() => setShow(false)}
        dialogClassName="modal-90w"
        aria-labelledby="example-custom-modal-styling-title"
      >

<div className='MapModal'>

<div className="LogoImage"><img src= {MapData[Area].logoURL} alt="로고 이미지 로드 오류"/></div>
       
        <Modal.Body>
        <div className="BGM"><BGM/></div>

        <h1 style={{fontFamily:"'SOGANGUNIVERSITYTTF", paddingTop:"1%"}}>&nbsp;{MapData[Area].title}</h1>
        <div className='MapText' style={{fontFamily:"'SOGANGUNIVERSITYTTF"}}>
          
        <h3>&nbsp;&nbsp;&nbsp;{MapData[Area].catchphrase}</h3>

        <div className='HashText'  style={{fontFamily:"'SOGANGUNIVERSITYTTF"}}>   
        <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{MapData[Area].content}</p>
        </div>

       <div className='contentText'>
       <h6 className='FormerText' style={{fontStyle:"italic"}}>{MapData[Area].text2}</h6> 
       <p>{MapData[Area].text}</p> 
       <p>{MapData[Area].text3}</p> 
       <p>{MapData[Area].text4}</p> 
       </div>
        
          </div>
        </Modal.Body>
        </div>
      </Modal>
    </>
  );
    }




}





export default Map;