import HTMLFlipBook from "react-pageflip";
import { React } from 'react'
import story from "./Story";
import { useState } from 'react';



function StoryBook() {

  function ClickToWorld(e){
    e.preventDefault();
    window.location.href='https://docs.google.com/document/d/11J-_mNMFeHVnU_0HXWMUmCMplaKnMRnitugUozxr1Wk/edit?usp=sharing'
  }; 


  return (
    <div className="BookContainer">
       <div className="NoMob"><img src={'https://i.ibb.co/QcpTXHG/PCONLY.png'} width="100%" /></div>
    <HTMLFlipBook className="StoryBook" width="640" height="720">
      <div className="StoryPage" id="BookNotice"></div>
      <div className="StoryPage" id="BookCover"></div>
      <div className="StoryPage"> <img className="BookPage1" src={'https://i.ibb.co/fps6hZn/Book-Page1.png'} width="100%" /><div className="StoryContent">{story[1].content}</div></div>
      <div className="StoryPage"> <img className="BookPage1" src={'https://i.ibb.co/RB3tGSn/Book-Page3.png'} width="100%" /><div className="StoryContent">{story[2].content}</div></div>
      <div className="StoryPage"> <img className="BookPage1" src={'https://i.ibb.co/PGLHcT9/Book-Page2.png'} width="75%" /><div className="StoryContent">{story[3].content}</div></div>
      <div className="StoryPage"> <img className="BookPage1" src={'https://i.ibb.co/sbY0RK9/Book-Page4.png'} width="75%" /><div className="StoryContent">{story[4].content}</div></div>
      <div className="StoryPage"> <img className="BookPage1" src={'https://i.ibb.co/y5tf0Hp/Book-Page5.png'} width="75%" /><div className="StoryContent">{story[5].content}</div></div>
      <div className="StoryPage"> <img className="BookPage1" src={'https://i.ibb.co/ZzyWB3t/Book-Page6.png'} width="100%" /><div className="StoryContent6"><strong>{story[6].content}</strong></div></div>
      <div className="StoryPage"> <img className="BookPage1" src={'https://i.ibb.co/VLckknm/Book-Page7.png'} width="90%" /><div className="StoryContent">{story[7].content}</div></div>
      <div className="StoryPage"> <img className="BookPage1" src={'https://i.ibb.co/k84Jm14/Book-Page9.png'} width="50%" /><div className="StoryContent">{story[8].content}</div></div>
      <div className="StoryPage"> <img className="BookPage1" src={'https://i.ibb.co/Cnkg3df/Book-Page8.png'} width="90%" /><div className="StoryContent">{story[9].content}</div></div>
      <div className="StoryPage"> <img className="BookPage1" src={'https://i.ibb.co/LvBwFz7/Book-Page11.png'} width="50%" /><div className="StoryContent">{story[10].content}</div></div>
      <div className="StoryPage"> <img className="BookPage1" src={'https://i.ibb.co/0KVH9Jz/Book-Page10.png'} width="65%" /><div className="StoryContent">{story[11].content}</div></div>
      <div className="StoryPage"> <div className="StoryContent6" style={{margin:"8%",padding:"8%"}}><strong>{story[12].content}</strong><button onClick={ClickToWorld}>세계관으로 워프</button></div></div>
      <div className="StoryPage" id="BookCover"></div>
    </HTMLFlipBook>
    </div>
  );



}
  export default StoryBook;

