import React, { useState,useEffect } from 'react';
import Typed from 'typed.js';
import styled from 'styled-components'

// let DialogBox = styled.span`
// position: relative;
// text-align: center;
// background: url("img/NPC_대화창.png");
// background-size: cover;
// font-family: "DungGeunMo", monospace;
// margin-inline: 4px;
// width: 1440px;
// height: 300px;
// overflow: hidden;
// margin: 0 auto;

// `


function Dialog() {

  let [count, setCount] = useState(0)
  let [Text, setText] = useState([
    '-1',
    '-2',
    '-3',
    '-4',
    '-5',
    '-6',
    '-7',
    '-8'
  ]);
  
  
  const el = React.useRef(null);


  React.useEffect(() => {
    const typed = new Typed(el.current, {
      strings: [Text[count]],
      typeSpeed: 60,
    });

    return () => {
      // Destroy Typed instance during cleanup to stop animation
      typed.destroy();

    };
  }, [count]);

  return (
        <div className="NPC">
    <img src={'https://i.ibb.co/J2M55kP/' + ({count}+1) + '.jpg'} width="100%" />
    <div className="DialogBox">
    <img src={'https://i.ibb.co/HHzypG4/Dialog-Box.png'} width="80%" />
    <div className="Dialog">
      <span ref={el} />
    </div>
    <div className="keepGo">{count}
        <button onClick={()=>{

if(count<7){setCount(count+1)}
else{setCount(0)} }}>버튼</button>
    </div>
    </div>
    </div>
   
   );
}

export default Dialog;