    text : "켈메스를 가장 잘 설명하는 자연은 북서부의 무어 산맥이다. 무어산맥은 그 험준함과 변덕스러움은 마수마저도 길을 잃게 해, 수도 켈메스를 천혜의 요새로 탈바꿈하게 만들었다. 명실상부 가장 안전한 도시이자 이븐디알의 아름다운 수도로, 제1기사단이 상주한다. 표준적인 사계절을 볼 수 있으며, 이븐디알에서 유일하게 합법적으로 카지노와 경매장을 운영하고 있기도 하다. 켈메스인들은 유독 서로에 대한 예의를 중시해 서로의 선을 잘 넘지 않는 사람들이 일반적이다. 예의바르고 친절하지만 친해지기는 어려운 사람이라는 인상. 토박이들은 몸의 어느 한 곳에 번개를 맞은 것 같은 흔적을 가진 채 태어난다는 것이 큰 특징이다. 또한 신경이 예민하거나 사람을 잘 믿지 않는 경우도 적지 않다. 하지만 그 덕에 사업수완이나 정치력, 기억력이 좋은 경우가 많다. 크로커스가 지천으로 피어 있는데, 그중에서도 보랏빛이 가장 흔하다. 사계절이 존재하다보니 넘치는 풍작은 기대하기 어렵더라도 블루베리와 석류만큼은 풍부하다. 다만 블루베리와 똑 닮은 ‘주목’도 지천이니 주의의 대상. 켈메스 땅 대부분이 비경작지라 이 외엔 먹을 수 있는 작물이 적어 대부분의 음식을 수입에 의존한다. 수도인 만큼 별도의 축제를 열지는 않지만 이븐디알의 축제인 크로커스제의 주최지이자 개최지이다. 보랏빛 크로커스가 뒤덮는 봄에 열리어 크로커스 꽃다발과 선 물을 건네며 무탈히 겨울을 보냈음을 축하하고 마음에 담아둔 이에게 마음을 전한다.  마지막 날에는 무도회를 즐긴다.",
    text2 :" 「방벽 서는 땅 켈메스, 제국의 수도이자 이븐디알의 시작지인 이곳을 두고 사람들은 방벽 서는 땅 켈메스 혹은 최후의 결전지 켈메스라고 불렀다. 켈메스는 최초의 이븐디알이 시작된 곳이자 백년 전쟁의 마지막 전투가 있던 땅이었다. 이븐디알의 시황제는 부서져가던 방패를 내리꽂으며 자신의 등 뒤를 지키겠다고 선언했고, 그 맹세를 지킨 끝에 지금의 이븐디알이 탄생했다. 황제는 그날의 맹세를 지금도 잊지 않아 이븐디알의 모든 기사는 켈메스 나이옐 숲의 훈련장을 거쳤고 켈메스는 이븐디알을 지키는 그 어느 것보다 강력한 방패로 남게 된다. 이븐디알을 지키는 땅, 최후의 맹세가 여전히 살아있는 곳. 이븐디알의 사람들은 이곳을, 이븐디알 의 가장 거대한 방벽이 서는 곳 켈메스라 불렀다.」",
