        text : "이븐디알의 동부에 위치해 있는 오플로트는 이븐디알에서 가장 넓은 목축지이자 다양한 자연을 볼 수 있는 곳이다. 따뜻한 날씨가 이어지고, 사계절이 분명하여 농경지로서 큰 역할을 하고 있다. 다양한 날씨 탓에 오플로트 출신들은 감으로 날씨를 맞추는 일에 능하지만, 비가 잦은 지역이니 만큼 우산을 필수적으로 생각하지는 않는다. 재배하는 건 곡식류가 대부분이고, 들국화와 라넌큘러스가 흔히 분포한다. 목축과 농경의 안정으로 유제품과 주류 또한 뛰어나다. 다양한 음식에 유제품이 사용되다 보니 느끼하다는 평이 다소 있기도 할 정도.약초가 흔하고 사냥이 많이 이루어지니 지역색의 민간요법이 특색적이다. 식생이 안정적인 덕에 오플로트 출신은 대개 타고나기를 기골이 장대하고 신체능력이 좋다. 피부가 어둡고 탄탄한 경우가 많고, 별을 잘 읽는다는 것 또한 오플로트 출신의 특성. 큰 마을을 제외하면 마을 간의 거리가 넓다보니 가까운 이웃이 몇 되지 않아 공동육아를 하는 경우가 있을 정도로 이웃과 돈독하다는 것 또한 한 가지 특성이 될 수 있을 것이다. 늦가을이면 초여름의 과일로 담근 햇와인과 수확물로 2주 간의 '시스 축제'를 연다. 이 기간동안 어린아이에게 민들레와 라넌큘러스 화관을 씌워주고 들국화 꽃다발을 선물하는 풍습이 있다.",
        text2 :"「드넓은 초원과 들과 사막은 무릇 거대한 자연이 그렇듯 사람을 고양시키는 힘이 있었다. 들판을 내달리는 바람, 끝이 보이지 않는 사막의 하늘, 살아있는 것의 소리가 끊이지 않는 초원. 오플로트에서는 귀를 기울이면 쏟아질 듯 무수한 별과 바람과 모래의 소리를 들을 수 있었다. 그럼에도 그 모든 것에 앞서 사람의 박동과 호흡이 가장 시끄러웠다. 박동과 호흡이란 무릇 어느 사람의 것보다 내 안의 것 나의 것이 커, 오플로트의 사람이란 하나같이 스스로에 귀 기울이길 잘했다. 밤이면 호흡과 박동이 시끄러울 정도로 숨막히게 적막했으나 살아 뒤섞이는 낮이 되면 죽어있던 이들이 되살아나듯 오플로트는 더할 것 없이 소란했다. 마른 풀을 밟으며 목을 축이며 다른 것을 사냥하며 혹은 먹어치워가며 살아 있음을 알리는 많은 것들 때문에. 오플로트의 밤은 많은 것을 죽게 했고 낮은 많은 것을 살게 했는데, 그 생사의 고락이 사람의 마음을 흔들어서, 그만 마음을 빼앗기게 해서. 오플로트를 거친 모든 이들은 오플로트를 사람을 고양시키는 곳이라고 이야기했다. 사람의 마음을 쥐고 흔들며 지나간 것을 돌이키게 만들더니 기어이 귀 기울이게 해 결국 마주한 모든 사람이 이 곳을 잊을 수 없게 한다고. 그것만이 오플로트라고. 사람을 흔들어 차오른 감정을 결코 잊을 수 없게 하는 곳, 사람을 고양시키게 하는 땅. 오플로트.」",      
