        content :"#과수원 #선량함 #요정에게 사랑받는 자",
        text : "이븐디알의 동쪽 끝, 이븐디알에서 가장 작은 땅이자 가장 고요한 곳으로 요정의 땅이라는 이명으로도 불린다. 눈과 바람이 드물고 사계절이 약세해 온화한 기후가 유지된다. 이븐디알에서 나는 과일의 8할이 헤브니센에서 자라고, 대부분의 땅에는 과수원이 지어져 있거나 관련 시설이 즐비하다. 다른 지역보다 꽃도 유달리 망울이 크고 자주, 다양하게 피는데 개중에서도 리시안셔스, 캄파눌라, 히아신스가 특히 많다.그 땅의 축복을 받은 덕분인지 헤브니센은 사람들 역시 체격이 크고, 언제나 다른 지역의 또래에 비해 뛰어난 발육상태를 자랑한다. 헤브니센 사람은 바보 같을 정도로 순박하고 느긋하다고 비꼴 수 있지만 헤브니센 사람의 타고난 무력 앞에서 그렇게 비꼬는 사람은 많지 않다. 또 의외로 타 지역민에 비해 감이 좋은 편인데, 직감보다는 예감에 치중되어 있어서 예지몽을 꾼다거나 어쩐지 감이 좋지 않아서 피했던 장소에 나쁜 일이 생긴다거나 하는 종류다. 헤브니센 출신은 옅은 색의 머리 색과 눈 색을 가진 경우가 많은데, 이런 특성은 감이 좋을수록 그러한 경향성을 보이기도 한다. 이웃한 다른 땅이 없기에 오플로트와 가장 활발하게 교류하고, 사이가 좋은 편이다. 두 지역의 유력 가문은 오래전부터 긴밀하게 교류했고 지금도 오플로트는 곡식류를, 헤브니센은 과일류를 나눈다. 타지역과 달리 계절마다, 일 년에 4번 가량의 축제를 연다. 각기 옛 언어에서의 사계를 의미하던 포라렛, 포이서, 휴스트, 로멜트로 불리는데, 작물의 풍작을 기원하는 것은 상통하다.",
        text2 :"「헤브니센은 이븐디알에서 가장 많은 꽃이 자라는 곳이었다. 그 오플로트조차 헤브니센을 이기지는 못했다. 색색의 아름다움. 헤브니센은 동화의 축복이 내려졌다는 소문이 떠도는 곳 답게 풍요로웠다. 오플로트와 맞닿은 고넷 해의 해변에서는 가장 아름다운 별을 볼 수 있었고, 여름이 끝날 무렵이면 과일이 익어가는 단내가 잠을 깨웠다. 손만 뻗으면 단 과실을 얻었고 풀숲에 늘어져 잠이 들어도 뱀조차 사람을 해치지 않았다. 안락하고 좁은 낙원. 평화롭고 아름다운 성소. 무엇이든 만개하고 어느 것이든 묵직하게 자라는 땅, 헤브니센은 그야말로 동화가 가꾸는 요정의 정원이었다.」",      
